import Link from 'next/link';
import { getTranslations } from 'next-intl/server';

export default async function HowItWorksPage() {
  const t = await getTranslations('howItWorks');

  const steps = [
    { num: '1', title: t('steps.connect'), desc: t('steps.connectDesc') },
    { num: '2', title: t('steps.choose'), desc: t('steps.chooseDesc') },
    { num: '3', title: t('steps.buy'), desc: t('steps.buyDesc') },
    { num: '4', title: t('steps.allocate'), desc: t('steps.allocateDesc') },
    { num: '5', title: t('steps.hold'), desc: t('steps.holdDesc') },
    { num: '6', title: t('steps.verify'), desc: t('steps.verifyDesc') },
  ];

  return (
    <>
      <section className="mobile-pt" style={{ paddingTop: '160px', paddingBottom: '80px', position: 'relative', overflow: 'hidden' }}>
        <div style={{ position: 'absolute', top: '20%', right: '-5%', width: '400px', height: '400px', background: 'radial-gradient(circle, rgba(226,184,87,0.08) 0%, transparent 70%)', filter: 'blur(60px)' }}></div>
        
        <div className="mobile-padding" style={{ maxWidth: '800px', margin: '0 auto', padding: '0 24px' }}>
          <div style={{ textAlign: 'center', maxWidth: '600px', margin: '0 auto 64px auto' }}>
            <h1 className="section-title" style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '48px', fontWeight: 700, color: 'white', marginBottom: '20px', letterSpacing: '-0.02em' }}>
              {t('title')} <span className="text-gold-gradient">{t('titleHighlight')}</span> {t('titleEnd')}
            </h1>
            <p style={{ fontSize: '17px', color: '#71717A', lineHeight: 1.7 }}>{t('subtitle')}</p>
          </div>

          {/* Steps */}
          <div style={{ display: 'flex', flexDirection: 'column', gap: '12px', marginBottom: '64px' }}>
            {steps.map((step) => (
              <div 
                key={step.num}
                style={{ 
                  display: 'flex', alignItems: 'flex-start', gap: '16px',
                  background: 'rgba(255,255,255,0.02)', border: '1px solid rgba(255,255,255,0.06)',
                  padding: '20px 24px', borderRadius: '16px'
                }}
              >
                <div style={{ 
                  width: '32px', height: '32px', borderRadius: '50%',
                  background: 'linear-gradient(135deg, #E2B857 0%, #C9A227 100%)',
                  display: 'flex', alignItems: 'center', justifyContent: 'center',
                  color: '#0B1121', fontWeight: 700, fontSize: '14px',
                  fontFamily: "'Space Grotesk', sans-serif", flexShrink: 0
                }}>{step.num}</div>
                <div>
                  <h3 style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '18px', fontWeight: 600, color: 'white', margin: '0 0 6px 0' }}>{step.title}</h3>
                  <p style={{ color: '#71717A', fontSize: '14px', margin: 0, lineHeight: 1.6 }}>{step.desc}</p>
                </div>
              </div>
            ))}
          </div>

          {/* CTA */}
          <div style={{ textAlign: 'center' }}>
            <h2 className="section-title" style={{ fontFamily: "'Space Grotesk', sans-serif", fontSize: '32px', fontWeight: 700, color: 'white', marginBottom: '24px' }}>
              {t('ctaTitle')} <span className="text-gold-gradient">{t('ctaHighlight')}</span>?
            </h2>
            <div style={{ display: 'flex', justifyContent: 'center', gap: '12px', flexWrap: 'wrap' }}>
              <Link href="https://wallet.auxite.io" style={{ 
                display: 'inline-flex', alignItems: 'center', gap: '8px',
                background: 'linear-gradient(135deg, #E2B857 0%, #C9A227 100%)',
                color: '#0B1121', padding: '16px 32px', borderRadius: '12px',
                fontWeight: 600, fontSize: '15px', textDecoration: 'none',
                boxShadow: '0 8px 32px rgba(226,184,87,0.3)'
              }}>
                {t('startTrading')}
                <svg style={{ width: '18px', height: '18px' }} fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M17 8l4 4m0 0l-4 4m4-4H3" /></svg>
              </Link>
              <Link href="/whitepaper" className="glass" style={{ 
                display: 'inline-flex', alignItems: 'center',
                color: 'white', padding: '16px 28px', borderRadius: '12px',
                fontWeight: 500, fontSize: '15px', textDecoration: 'none'
              }}>
                {t('whitepaper')}
              </Link>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
